document.addEventListener('DOMContentLoaded', function () {
    iniciarApp();
});
function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}
function navegacionFija(){
    //TODO Seleccionamos la clase del elemento que vamos a fijar
    const barra=document.querySelector('.header');
    //TODO Seleccionamos la clase del elemento que vamos a usar 
    const sobreFestival=document.querySelector('.sobre-festival');
    const body=document.querySelector('body');
    //TODO A partir del scroll, realizamos una funcion
    window.addEventListener('scroll', function(){
        //TODO Siempre estaremos revisando la posicion de sobreFestival
        //? getBoundingClientRect(): es una funcion que da informacion 
        //console.log(sobreFestival.getBoundingClientRect());
        //TODO top<0 significa que cunado el alto de el contenido toque el limite superior de la pantalla  hacer procedimiento
        if(sobreFestival.getBoundingClientRect(). top<0){
            barra.classList.add('fijo');//TODO Para fijar el header
            body.classList.add('body-scroll');//TODO Para fijar o solucionar el problema del body
        
        }else{
            barra.classList.remove('fijo');//TODO Para remover lo de fijar el header        
            body.classList.remove('body-scroll');//TODO Para remover lo de fijar el body        
        }
    });
}
function scrollNav() {
    //TODO Seleccionamos la clase del elemento que vamos a usar
    const enlaces = document.querySelectorAll('.navegacion-principal a'); //Para leer los enlaces o seleccionar los enlaces
    enlaces.forEach(enlace => {
        enlaces.addEventListener('click', function (e) {
            e.preventDefault();//Para prevenir el comportamiento por default de e evento

            //todo Nuevo comportamiento
            const seccionScroll=e.target.attributes.href.value;
            const seccion=document.querySelector(seccionScroll);
            seccion.scrollIntoView({behavior:"smooth"});
            //target es lo que le hemos dado click
            //attributes es para mostrar los atributos
            //href para mostrar los hrfef
            //value para mostrar el valor de href
            //console.log(e.target.attributes.href.value); 
        });

    });
}


function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');
    for (let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture'); //Generamos 12 codigos picture y llevaran por dentro el siguiente codigo
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img width="200" height="300" src="img/thumb/${i}.jpg" alt="imagen del vocalista del festival">
        `;
        imagen.onclick = function () { //Con un clic en imagen se ejecutara el siguiente codigo
            mostrarImagen(i);
        }
        galeria.appendChild(imagen); //Para mostrar cada imagen
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture'); //Generamos 12 codigos picture y llevaran por dentro el siguiente codigo
    imagen.innerHTML = `
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img width="200" height="300" src="img/grande/${id}.jpg" alt="imagen del vocalista del festival">
    `;
    //Crea el Overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay'); //creacion de una clase 'overlay' para darle estilos scss

    //Para que el overlay se elimine el mismo al presionar por fuera del overlay o en el overlay
    overlay.onclick = function () { //Con un click en la x hacer....
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); //Para remover eso de quitar scrooll
        overlay.remove(); //Para remover el overlay 


    };

    //Cerrar el modal(ventana)
    const cerrarModal = document.createElement('P');
    cerrarModal.textContent = 'X';
    cerrarModal.classList.add('btn-cerrar');
    cerrarModal.onclick = function () { //Con un click en la x hacer....
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); //Para remover eso de quitar scrooll
        overlay.remove(); //Para remover el overlay 


    };
    overlay.appendChild(cerrarModal); //para que el cerraModal forme parte del overlay




    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body'); //Para que no se pueda mover el body cunado se aplica el overlay

}